.root {
    width: 300px;
    height: 500px;
    padding: 20px;
    background-color: #FFFFFF;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;

}

.card {
    width: 300px;
    height: 400px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    
    // margin-bottom: 20px;
    cursor: pointer;
}

.root:hover {
    box-shadow: 0px 0px 20px 20px rgba(0, 0, 0, 0.08);
    transform: translateY(-5px);
}

.image {
    width: 90%;
    height: 60%;
    padding: 10px;

    img {
        object-fit: cover;
        width: 100%;
        height: 100%;
    }
}

.title {
    padding: 5% 5%;
    text-align: center;
    font-size: 1em;
    font-weight: 600;
}

.price{
    font-size: 1.5em;
    font-weight: 700;
}

.toCart {
    width: 150px;
    max-height: 20px;
    padding-top: 5%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    .qwantity {
        font-size: 24px;
        font-weight: 800;
    }

    .button {
        width: 30px;
        height: 30px;
    }
}

